<template>
  <div class="forget-pwd">
    <!--导航内容开始-->
    <header>
      <div class="navigation container clearfix">
        <div class="pull-left">
          <router-link class="logo" to="/"><img src="static/images/index/logo_02.png" alt="" /></router-link>
          <span>{{ $t('forgetPwd.nav.title') }}</span>
        </div>
        <div class="menu2 pull-right">
          {{ $t('forgetPwd.des.part1') }}<router-link to="/login" class="menu2-login">{{ $t('forgetPwd.des.part2') }}</router-link><span class="icon-back"></span>&nbsp;<router-link to="/" class="back-index">{{ $t('forgetPwd.des.part3') }}</router-link>
        </div>
      </div>
    </header>
		<!--导航内容结束-->

    <div class="form">
      <el-tabs v-model="activeName">
        <el-tab-pane :label="$t('forgetPwd.byPhone')" name="phone"> 
          <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="0" class="demo-ruleForm">
            <el-form-item prop="user_account">
              <el-input v-model.trim="ruleForm.user_account" :placeholder="$t('forgetPwd.placeholder.tel')"></el-input>
            </el-form-item>
            <el-form-item prop="sms_verify" class="verify">
              <el-input type="text" auto-complete="off" :placeholder="$t('forgetPwd.placeholder.verifyCode')" v-model.trim="ruleForm.sms_verify"></el-input><el-button class="get-verify" @click="sendRequestSms($event)" :disabled="!validCode">{{ validTip }}</el-button>
            </el-form-item>
            <el-form-item  prop="user_password">
              <el-input type="password" v-model.trim="ruleForm.user_password"  :placeholder="$t('forgetPwd.placeholder.newPwd')"></el-input>
            </el-form-item>
            <el-form-item  prop="user_confirmpwd">
              <el-input type="password" v-model.trim="ruleForm.user_confirmpwd"  :placeholder="$t('forgetPwd.placeholder.confirmPwd')" @keyup.enter.native="submitForm('ruleForm')"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('ruleForm')">{{ $t('forgetPwd.submit') }}</el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane :label="$t('forgetPwd.byEmail')" name="email">
          <el-form :model="ruleForm1" status-icon :rules="rules1" ref="ruleForm1" label-width="0" class="demo-ruleForm">
            <el-form-item prop="user_account">
              <el-input v-model.trim="ruleForm1.user_account" :placeholder="$t('forgetPwd.placeholder.email')"></el-input>
            </el-form-item>
            <el-form-item prop="sms_verify" class="verify">
              <el-input type="text" auto-complete="off" :placeholder="$t('forgetPwd.placeholder.verifyCode')" v-model.trim="ruleForm1.sms_verify"></el-input><el-button class="get-verify" @click="sendRequestSms($event)" :disabled="!validCode1">{{ validTip1 }}</el-button>
            </el-form-item>
            <el-form-item  prop="user_password">
              <el-input type="password" v-model.trim="ruleForm1.user_password"  :placeholder="$t('forgetPwd.placeholder.newPwd')"></el-input>
            </el-form-item>
            <el-form-item  prop="user_confirmpwd">
              <el-input type="password" v-model.trim="ruleForm1.user_confirmpwd"  :placeholder="$t('forgetPwd.placeholder.confirmPwd')" @keyup.enter.native="submitForm('ruleForm1')"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('ruleForm1')">{{ $t('forgetPwd.submit') }}</el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
      </el-tabs>
    </div>
    
    <footer>
      <div class="">
        {{ $t('register.footer') }}
      </div>
    </footer>
  </div>
</template>
<script>
  import { Phone, loginPwd, Email } from '@/utils/validate'
  import { resetPassword, sendRequestSms } from '@/api/loginModule'
  import md5 from 'js-md5'
  export default {
    data() {
      const validatePassword = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(this.$t('errorTips.withoutPwd')))
        } else if (value.length < 6) {
          callback(new Error(this.$t('errorTips.longPwd')))
        } else if (!loginPwd(value)) {
          callback(new Error(this.$t('errorTips.rulePwd')))
        } else if (value.length > 20) {
          callback(new Error(this.$t('errorTips.longLimitPwd')))
        } else {
          callback()
        }
      }
      const validateUserName = (rule, value, callback) => {
        if (this.activeName === 'phone') {
          if (value === '') {
            callback(new Error(this.$t('errorTips.withoutPhone')))
          } else {
            if (Phone(value)) {
              callback()
            } else {
              callback(new Error(this.$t('errorTips.phoneFormat')))
            }
          }
        }
        if (this.activeName === 'email') {
          if (value === '') {
            callback(new Error(this.$t('errorTips.withoutEmail')))
          } else {
            if (Email(value)) {
              callback()
            } else {
              callback(new Error(this.$t('errorTips.emailFormat')))
            }
          }
        }
      }
      const validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(this.$t('errorTips.surePwd')))
        } else if (value !== this.ruleForm.user_password) {
          callback(new Error(this.$t('errorTips.surePwdError')))
        } else {
          callback()
        }
      }
      const validatePass1 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(this.$t('errorTips.surePwd')))
        } else if (value !== this.ruleForm1.user_password) {
          callback(new Error(this.$t('errorTips.surePwdError')))
        } else {
          callback()
        }
      }
      return {
        proto: false,
        activeName: 'phone',
        ruleForm: {
          user_account: '',
          user_password: '',
          sms_verify: '',
          user_confirmpwd: ''
        },
        ruleForm1: {
          user_account: '',
          user_password: '',
          sms_verify: '',
          user_confirmpwd: ''
        },
        rules: {
          user_account: [
            { validator: validateUserName, trigger: 'blur' }
          ],
          user_password: [
            { validator: validatePassword, trigger: 'blur' }
          ],
          sms_verify: [
            { required: true, message: this.$t('errorTips.withoutVerifyCode'), trigger: 'blur' }
          ],
          user_confirmpwd: [
            { validator: validatePass2, trigger: 'blur' },
            { required: true, message: this.$t('errorTips.withoutSurePwd'), trigger: 'blur' }
          ]
        },
        rules1: {
          user_account: [
            { validator: validateUserName, trigger: 'blur' }
          ],
          user_password: [
            { validator: validatePassword, trigger: 'blur' }
          ],
          sms_verify: [
            { required: true, message: this.$t('errorTips.withoutVerifyCode'), trigger: 'blur' }
          ],
          user_confirmpwd: [
            { validator: validatePass1, trigger: 'blur' },
            { required: true, message: this.$t('errorTips.withoutSurePwd'), trigger: 'blur' }
          ]
        },
  
        validCode: true,
        validState: true,
        validTip: this.$t('verifyCode.title'),
        validCode1: true,
        validState1: true,
        validTip1: this.$t('verifyCode.title')
      }
    },
    mounted() {
      document.body.className = 'bgWhite'
    },
    beforeRouteLeave(to, from, next) {
      document.body.className = ''
      next()
    },
    methods: {
      accountVerify(value) {
        if (this.activeName === 'phone') {
          if (value === '') {
            this.$message.error(this.$t('errorTips.withoutPhone'))
            return false
          } else {
            if (!Phone(value)) {
              this.$message.error(this.$t('errorTips.phoneFormat'))
              return false
            }
          }
        }
        if (this.activeName === 'email') {
          if (value === '') {
            this.$message.error(this.$t('errorTips.withoutEmail'))
            return false
          } else {
            if (!Email(value)) {
              this.$message.error(this.$t('errorTips.emailFormat'))
              return false
            }
          }
        }
        return true
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log('验证成功')
            let data
            if (this.activeName === 'phone') {
              data = { ...this.ruleForm }
              data.user_password = md5(this.ruleForm.user_password)
              data.user_confirmpwd = md5(this.ruleForm.user_confirmpwd)
            } else {
              data = { ...this.ruleForm1 }
              data.user_password = md5(this.ruleForm1.user_password)
              data.user_confirmpwd = md5(this.ruleForm1.user_confirmpwd)
            }
            resetPassword(data).then(res => {
              console.log('res', res)
              if (res.status !== 1000) {
                this.$message.error(res.msg)
                return false
              }
              this.$confirm(this.$t('forgetPwd.tips.part1'), this.$t('forgetPwd.tips.title'), {
                confirmButtonText: this.$t('forgetPwd.tips.sure'),
                cancelButtonText: this.$t('forgetPwd.tips.cancle'),
                type: 'success'
              }).then(() => {
                this.$router.push('/login')
              }).catch(() => {
              })
            }).catch(() => {
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      sendRequestSms(event) {
        let flag
        const data = {}
        let state = ''
        if (this.activeName === 'phone') {
          state = 'phone'
          flag = this.accountVerify(this.ruleForm.user_account)
          data.user_account = this.ruleForm.user_account
        } else {
          state = 'email'
          flag = this.accountVerify(this.ruleForm1.user_account)
          data.user_account = this.ruleForm1.user_account
        }
  
        data.request_type = 'reset'
        if (flag) {
          this.setTime(state)
          sendRequestSms(data).then(res => {
            console.log('res', res)
            if (res.status !== 1000) {
              this.$message.error(res.msg)
              return false
            }
          }).catch(() => {
          })
        }
      },
      setTime(str) {
        if (str === 'phone') {
          let time = 60
          if (this.validCode) {
            this.validCode = false
            const _this = this
            const t = setInterval(function() {
              time--
              _this.validTip = time + _this.$t('verifyCode.second')
              if (time === 0) {
                clearInterval(t)
                _this.validTip = _this.$t('verifyCode.getAgain')
                _this.validCode = true
                _this.validState = true
              }
            }, 1000)
            return t
          }
        } else {
          let time = 60
          if (this.validCode1) {
            this.validCode1 = false
            const _this = this
            const t = setInterval(function() {
              time--
              _this.validTip1 = time + _this.$t('verifyCode.second')
              if (time === 0) {
                clearInterval(t)
                _this.validTip1 = _this.$t('verifyCode.getAgain')
                _this.validCode1 = true
                _this.validState1 = true
              }
            }, 1000)
            return t
          }
        }
      }
    }
  }
</script>
<style lang="less" scoped>
  .forget-pwd{
    .demo-ruleForm{
      /deep/ .el-form-item{
        margin-bottom: 30px;
      }
    }
    header {
      background: white;
      box-shadow: 0 1px 15px 0px #f3f3f4;
      width: 100%;
      height: 90px;
    }
    .navigation {
      color: white;
      line-height: 90px;
    }
    .navigation .menu2 {
      color: #2f3032;
      font-size: 12px;
    }
    .navigation .menu2 a {
      color: #2f3032;
    }
    .navigation .menu2 .menu2-login {
      font-size: 12px;
      color: #1fa8f6;
      padding: 0 10px 0 7px;
    }
    .navigation > .pull-left > span {
      color: #2f3032;
      font-size: 18px;
      display: inline-block;
      margin-left: 20px;
      padding-left: 14px;
      border-left: 1px solid #c7cbd1;
      height: 20px;
      line-height: 20px;
      margin-top: 40px;
    }
    .logo img {
      margin: 18px 10px 24px 0;
      vertical-align: middle;
      border: 0;
    }
    .navigation .pull-left span{
      color: #2f3032;
      font-size: 18px;
      display: inline-block;
      margin-left: 20px;
      padding-left: 10px;
      border-left: 1px solid #c7cbd1;
      height: 20px;
      line-height: 20px;
      margin-top: 40px;
    }
    .form{
      width: 340px;
      margin: 0 auto;
      margin-top: 165px;
      .el-checkbox__label a{
        color: #1fa8f6;
      }
    }
    footer {
      text-align: center;
      color: #94969b;
      font-size: 12px;
      border-top: 1px solid #f0f0f4;
      padding-top: 35px;
      margin-top: 70px;
    }
    .tips {
      position: absolute;
      left: 350px;
      top: 0px;
      color: #d2d0d5;
      width: 500px;
      font-size: 12px;
      line-height: 40px;
      img{
        margin-right: 5px;
        margin-top: 12px;
        display: block;
        float: left;
      }
    }
    .el-button--primary{
      width: 100%;
    }
    /deep/ .verify .el-input__inner,
    .verify .el-input{
      width: 213px;
    }
    .get-verify {
      width: 110px;
      margin-left: 15px;
      display: inline-block;
      padding: 12px 13px;
      background: rgba(23, 100, 232, .15);
      color: rgba(23, 100, 232, .7);
    }
    /deep/ .el-checkbox__input.is-checked + .el-checkbox__label {
      color: #606266;
    }
    /deep/ .el-tabs__item {
      width: 170px;
      padding: 0;
      text-align: center;
      margin-bottom: 10px;
      font-size: 16px;
    }
    /deep/ .el-form-item__content{
      width: 100%;
    }
    /deep/ .el-tabs__active-bar,
    /deep/ .el-tabs__nav-wrap::after{
      height: 3px;
      border-radius: 3px;
    }
    /deep/ .el-tabs__active-bar{
      width: 170px !important;
    }
 }
</style>



